import { useEffect, useRef } from "react";

const ToKnockoutRounds = (props) => {
  const scrollToRef = useRef();

  useEffect(() => {
    if (scrollToRef.current) {
      window.scrollTo({
        behavior: "smooth",
        top: document.body.scrollHeight,
      });
    }
  }, []);

  return (
    <div
      className="to-knockout-rounds absolute bottom-4 w-full cursor-pointer"
      onClick={props.onClick}
      ref={scrollToRef}
    >
      <div className="to-knockout-rounds-text text-slate-400 text-md font-semibold tracking-wide animate-bounce cursor-pointer">
        Proceed to the Knockout Stage...
      </div>
      <div className="arrow">
        <span></span>
        <span></span>
      </div>
    </div>
  );
};

export default ToKnockoutRounds;
